<template>
  <div class="phoneDoorManager">
    <!-- 头部 -->
    <navigation-bar @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth">
      <template v-slot:nav-right>
        <span class="rightBtn" @click="onClickRight">?</span>
      </template>
    </navigation-bar>
    <div class="phoneDoorManagerContent">
      <div v-for="(area,index) in areaList" :key="index">
        <div class="areaCell">
          <div class="areaTitle" @click="showDoors(area)">{{area.area_name}}</div>
        </div>
        <div  v-if="area.isShowDoors">
          <div  class="doorCell" v-for="(door,doorIndex) in area.doorList" :key="doorIndex">
            <div class="doorTitle">{{door.deviceName}}</div>
            <van-switch
              v-model="door.isDftDoor"
              active-color="#FB5768"
              inactive-color="#D1D1D1"
              size="26px"
              style="margin:8px 10px 0 0;"
              @change="setDoorRemote(area,area.doorList,doorIndex)"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- <propertyNone v-show="phoneDoorManagerDefaule && phoneInfor.length <= 0" :title="defaultTitle"></propertyNone> -->
  </div>
</template>

<script>
import { getDoorListService,getOwnerDoorListService,cancelDftDoorService,setDftDoorService } from "../../services/phoneDoorService";
import { queryAreaInfoService } from "../../services/propertyBillService";
import propertyNone from "./propertyNone";
import NavigationBar from "@/common/currency/NavigationBar.vue";
import { Dialog } from 'vant';
export default {
  name: "phoneDoorManager",
  mounted() {
    this.getAraeList();
  },
  data() {
    return {
      areaList: [],
      activeNames: ["1"],
      headerItemWidth: {
        width: "84px"
      },
      title: "设置默认进出门",
      phoneImg: require("../../assets/images/personal_center/Call_small.png")
    };
  },
  components: {
    propertyNone,
    NavigationBar
  },
  methods: {
    getAraeList() {
      let reqData = {
        owner_code: this.$userInfo.OWNER_CODE
      };
      queryAreaInfoService({"data": JSON.stringify(reqData)})
        .then(result => {
          //请求成功
          if (result.result) {
            result.data.areaList.forEach(area => {
              area.isShowDoors = false;
              area.doorList = [];
            });
            this.areaList = result.data.areaList;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorManagerDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    showDoors(area) {
      /* 已显示门禁列表 */
      if (area.isShowDoors) {
        area.isShowDoors = false;
        return;
      }

      let reqData = {
        ownerCode: this.$userInfo.OWNER_CODE,
        areaCode: area.area_code
      };
      getOwnerDoorListService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode=='0') {
            if(result.data.length<=0){
              this.$toast("该小区暂未开放设置功能！");
              return;
            }
            area.isShowDoors = true;
            area.doorList = result.data;
            area.doorList.forEach(door => {
              door.isDftDoor = door.isDftDoor==1?true:false;
            });
          } else {
            this.$toast(result.msg);
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    setDoorRemote(area,doorList,doorIndex){
      //设置参数
      let reqData ={
        areaCode:area.area_code,
        ownerCode:this.$userInfo.OWNER_CODE,
        deviceCode:doorList[doorIndex].deviceCode,
        deviceMan:"2",
        createdBy:this.$userInfo.OWNER_CODE
      };
      //判断该门禁是否默认门禁
      if(!doorList[doorIndex].isDftDoor){
        //如果是默认门禁，调用取消方法
        this.cancelDftDoor(reqData);
      }else{
        //如果不是默认门禁，调用设置方法
        //设置前首先判断该小区是否设置其他门未默认门禁
        let isOnly = true;
        doorList.forEach(door => {
          if(door.isDftDoor==1&&door.deviceCode!=doorList[doorIndex].deviceCode){
            this.$toast("默认门禁只能设置一个");
            doorList[doorIndex].isDftDoor = 0;
            isOnly = false;
            return;
          }
        });
        if(isOnly)
          this.setDftDoor(reqData);

      }
    },
    setDftDoor(reqData){
      setDftDoorService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode=='0') {
            this.$toast("默认门禁设置成功");
          } else {
            this.$toast(result.msg);
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    cancelDftDoor(reqData){
      cancelDftDoorService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode=='0') {
            this.$toast("默认门禁取消成功");
          } else {
            this.$toast(result.msg);
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onClickLeft() {
      this.$router.back();
    },
    onClickRight(){
      Dialog.alert({
        title: '说明',
        message: '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;设置小区的默认进出门后，每次使用手机开门时，蓝牙在尝试开门(通常为2秒)未成功时，将自动通过网络远程开启默认进出门。保证业主快速进出小区的良好体验。\n&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;设置默认进出门之后，APP仍将弹出远程开门列表，不会影响远程开启其它门禁。\n&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;因为设置默认进出门后，默认进出门将自动打开，为了避免门禁反复无人开启，以及潜在的人员进出风险，建议业主每次习惯进出小区经过同一个大门时开启此配置。举例：小区有1号门、2号门两个大门都支持蓝牙开门、远程开门，业主每次都从1号门进出，那么可以开启此配置；如果每次从1号门进，2号门出，不建议开启此配置。'
      }).then(() => {
        // on close
      });
    }
  }
};
</script>

<style scoped>
.phoneDoorManager {
  background-color: #f9f9f9;
  height: 100%;
  display: flex;
  flex-direction: column;
}
/deep/.navBar .right {
  display: inline;
}
.phoneDoorManager .rightBtn {
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #ffffff;
  background-color: #dddddd;
  letter-spacing: -0.05px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  border-radius: 12px;
  text-align: center;
  float: right;
  margin-right: 10px;
  margin-top: 3px;
}
.phoneDoorManager .phoneDoorManagerContent {
  padding-top: 10px;
  flex: 1;
  overflow-y: scroll;
}

.phoneDoorManager .areaCell {
  height: 44px;
  line-height: 44px;
  background-color: #ffffff;
  margin-bottom: 10px;
  display: flex;
}
.phoneDoorManager .areaTitle {
  font-size: 14px;
  flex: 1;
  padding: 0 16px;
}
.phoneDoorManager .doorCell {
  height: 44px;
  line-height: 44px;
  /* background-color: #ffffff; */
  margin-bottom: 10px;
  display: flex;
}
.phoneDoorManager .doorTitle {
  font-size: 14px;
  flex: 1;
  padding: 0 16px;
}
.phoneDoorManager .van-dialog__message{
  text-align: left;
}
</style>
