//获取用户小区列表
const queryAreaInfoService = (params) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-property-payment/appComboboxData/queryAreaInfo', params, 'baseUrl').then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}
